:root {
  --yellow: hsl(47, 88%, 63%);
  --white: hsl(0, 0%, 100%);
  --grey: hsl(0, 0%, 50%);
  --black: hsl(0, 0%, 7%);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 30;
  font-family: "Figtree", sans-serif;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0rem;
  background-color: var(--yellow);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: var(--white);
  border: 0.125rem solid;
  font-size: 1rem;
  width: 90%;
  max-width: 25rem;
  height: auto;
  padding: 1.25rem;
  border-radius: 1rem;
  box-shadow: 0.75rem 0.75rem rgb(0, 1, 1);
  text-align: start;
}

.card__image {
  width: 100%;
  border-radius: 1rem;
  margin-bottom: 1.25rem;
}

.card__date {
  padding: 0.1875rem;
  margin-right: 9.0625rem;
  margin-bottom: 1.25rem;
}

button {
  background: var(--yellow);
  font-weight: 800;
  font-size: 1.0625rem;
  font-family: "Figtree", sans-serif;
  border-radius: 0.3125rem;
  margin-right: 14.375rem;
  margin-bottom: 1.25rem;
  height: 1.875rem;
  width: 5rem;
  padding: 0.1875rem;
}

h2 {
  margin-right: 1.875rem;
  margin-bottom: 1.25rem;
}

p {
  margin-bottom: 1.25rem;
}

.card__div {
  margin-right: 10rem;
  display: flex;
  align-items: center;
}

.card__div__avatar {
  width: 2.8125rem;
  height: 2.8125rem;
}

.card__div h4 {
  margin-left: 0.9375rem;
}